.about {
  margin-top: 15%;
  justify-content: space-between;
  align-items: center;
}
.about a {
  color: inherit;
}
.about__img {
  object-fit: contain;
  max-width: 100%;
  min-width: 375px;
  height: auto;
}
.about__info {
  font-family: "roboto", sans-serif;
  font-size: 16px;
  font-style: none;
  line-height: 24px;
  font-weight: 500;
  color: white;
  max-width: auto;
  min-width: 200px;
  height: auto;
}
.about__info h2 {
  font-size: 30px;
  font-weight: bold;
}
.about__donwload {
  margin-top: 20px;
  position: relative;
}
.about__donwloadBtn {
  outline: 0;
  margin-right: 0px !important;
  cursor: pointer;
  font-size: 1rem;
  background-color: rgb(255, 255, 255);
  padding: 8px 30px;
  font-weight: bold;
  border-radius: 20px;
  background-color: #0bceaf;
}
.about_tech {
  margin-top: 25px;
  justify-content: space-between;
  align-items: center;
}
.about_tech span {
  color: white;
  font-size: 14px;
}
.about_tech li {
  height: 35px;
  margin-left: 10px;
  margin-bottom: 7px;
  display: flex;
  align-items: center;
  border-style: solid;
  color: #11bb9f;
  border-width: 1px;
  padding: 1% 10px;
  border-radius: 7px;
}
.about__traits {
  overflow: hidden;
  background-color: #202026;
  box-shadow: -1rem 0 3rem #000;
  padding: 20px;
  border-radius: 8px;
  color: #fff;
}
.about__traits h3 {
  font-family: "roboto", sans-serif;
  font-weight: bold;
  margin-top: 0;
  font-size: 25px;
}
.fa-code {
  color: #ed7256;
  font-size: 30px;
  margin-bottom: 20px;
  margin-top: 16px;
}
.fa-server {
  color: #2796e2;
  font-size: 30px;
  margin-bottom: 20px;
  margin-top: 16px;
}
.fa-pencil-ruler {
  color: #1cb62e;
  font-size: 30px;
  margin-bottom: 20px;
  margin-top: 16px;
}
@media screen and (max-width: 768px) {
  .about__col1 {
    order: 1;
  }
  .about__col2 {
    order: 2;
  }
  .about {
    text-align: center;
  }

  
}
