.home {
  margin-top: 10%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home__tag {
  background-color: #0bceaf;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 1px;
  font-weight: bold;
  padding: 10px 30px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 0px;
  color: white;
  font-family: "roboto", sans-serif;
  overflow-x: hidden !important;
  text-align: left;
  width: fit-content;
}
.home__header {
  color: white;
  font-family: "roboto", sans-serif;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  text-align: left;
}
.home__header h2 {
  font-size: 60px;
  font-weight: 600;
  line-height: 65px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.home__header h4 {
  font-weight: bold;
  font-family: "roboto", sans-serif;
  color: white;
  font-size: 26px;
  line-height: 34px;
  margin-bottom: 30px;
}
.home__details {
  position: relative;
  list-style: none;
  font-family: "roboto", sans-serif;
  font-size: 14px;
  color: white;
}
.home__social a {
  color: inherit;
}
.home__social li {
  display: inline-block;
  color: white;
  list-style: none;
  list-style-type: disc;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 10px;
  padding-inline-end: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 15%;
}
.home__details i:hover {
  color: #11bb9f;
  transition: 0.7s;
}
.home__list {
  display: flex;
  justify-content: space-between;
  align-items: left;
}
.home__social li:hover {
  background-color: white;
  color: #0bceaf;
  transition: 0.5s;
}
.fa-envelope {
  padding-right: 15px;
}
.fa-phone {
  padding-right: 15px;
}
.fa-map-marker {
  padding-right: 15px;
}
.home__avatar {
  max-width: auto;
  max-height: 360px;
  border-radius: 50%;
  padding: 20px;
  background: #110f17;
  border: 20px solid #030304;
}

@media screen and (max-width: 768px) {
  .home__col1 {
    text-align: center;
    order: 1;
  }
  .home__col2 {
    order: 2;
  }
  .home__header h2 {
    text-align: center;
  }
  .home__header h4 {
    text-align: center;
  }
  .home__details {
    text-align: center;
  }
  .home__social {
    color: inherit;
    text-align: center;
  }
}
